@font-face {
  font-family: "TT Norms";
  src: local("TT Norms Regular"), local("TT-Norms-Regular"),
    url("../src/assets/fonts/TTNorms-Regular.woff2") format("woff2"),
    url("../src/assets/fonts/TTNorms-Regular.woff") format("woff"),
    url("../src/assets/fonts/TTNorms-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "TT Norms";
  src: local("TT Norms Medium"), local("TT-Norms-Medium"),
    url("../src/assets/fonts/TTNorms-Medium.woff2") format("woff2"),
    url("../src/assets/fonts/TTNorms-Medium.woff") format("woff"),
    url("../src/assets/fonts/TTNorms-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "Pixel";
  src: local("pixel digivolve cyrillic"), local("pixel-digivolve-cyrillic"),
    url("../src/assets/fonts/pixeldigivolvecyrillic.woff2") format("woff2"),
    url("../src/assets/fonts/pixeldigivolvecyrillic.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}
* {
  font-family: "TT Norms";
}
:root {
  --dark: #181818;
  --dark-blue: #13141c;
  --green: #37f713;
  --gray: #efefef;
  --grey-light: #f9f9f9;
  --white: #ffffff;
  --red: #ff0d0d;
  --transition: 0.3s;
  --input-label: rgba(24, 24, 24, 0.6);
  --input-placeholder: rgba(19, 20, 28, 0.4);
}
